export const getDateRange = (dateRangeType) => {
    const endDate = new Date();
    const startDate = new Date();
    let dateRange;
    switch (dateRangeType) {
        case "previous_month_to_date" /* previousMonthToDate */:
            startDate.setDate(1); // Required to obtain correct month
            startDate.setMonth(startDate.getMonth() - 1); // Note: Must include previous and current month
            dateRange = {
                end_date: format(endDate, 'yyyy-MM-dd'),
                start_date: format(startDate, 'yyyy-MM-dd'),
            };
            break;
        case "last_sixty_days" /* lastSixtyDays */:
            dateRange = getLast60DaysDate();
            break;
        case "last_thirty_days" /* lastThirtyDays */:
            dateRange = getLast30DaysDate();
            break;
        case "current_month_to_date" /* currentMonthToDate */:
        default:
            dateRange = getCurrentMonthDate();
            break;
    }
    return dateRange;
};
export const getDateRangeDefault = (queryFromRoute) => {
    return queryFromRoute.dateRange || "current_month_to_date" /* currentMonthToDate */;
};
export const getPerspectiveDefault = ({ awsProviders, awsProvidersFetchStatus, azureProviders, azureProvidersFetchStatus, gcpProviders, gcpProvidersFetchStatus, ibmProviders, ibmProvidersFetchStatus, ocpProviders, ocpProvidersFetchStatus, queryFromRoute, userAccess, }) => {
    let result = queryFromRoute.perspective;
    if (result) {
        return result;
    }
    const isLoading = awsProvidersFetchStatus === 1 /* inProgress */ ||
        azureProvidersFetchStatus === 1 /* inProgress */ ||
        gcpProvidersFetchStatus === 1 /* inProgress */ ||
        ibmProvidersFetchStatus === 1 /* inProgress */ ||
        ocpProvidersFetchStatus === 1 /* inProgress */;
    if (!isLoading) {
        if (isOcpAvailable(userAccess, ocpProviders, ocpProvidersFetchStatus)) {
            result = "ocp" /* ocp */;
        }
        else if (isAwsAvailable(userAccess, awsProviders, awsProvidersFetchStatus)) {
            result = "aws" /* aws */;
        }
        else if (isAzureAvailable(userAccess, azureProviders, azureProvidersFetchStatus)) {
            result = "azure" /* azure */;
        }
        else if (isGcpAvailable(userAccess, gcpProviders, gcpProvidersFetchStatus)) {
            result = "gcp" /* gcp */;
        }
        else if (isIbmAvailable(userAccess, ibmProviders, ibmProvidersFetchStatus)) {
            result = "ibm" /* ibm */;
        }
    }
    return result;
};
export const getGroupByDefault = (perspective) => {
    let result;
    switch (perspective) {
        case "aws" /* aws */:
        case "aws_ocp" /* awsOcp */:
        case "gcp" /* gcp */:
        case "gcp_ocp" /* gcpOcp */:
        case "ibm" /* ibm */:
            result = 'account';
            break;
        case "azure" /* azure */:
        case "azure_ocp" /* azureOcp */:
            result = 'subscription_guid';
            break;
        case "ocp" /* ocp */:
        case "ocp_cloud" /* ocpCloud */:
            result = 'project';
            break;
        default:
            result = undefined;
            break;
    }
    return result;
};
