export const getRouteForQuery = (history, query, reset = false) => {
    // Reset pagination
    if (reset) {
        query.filter = Object.assign(Object.assign({}, query.filter), { offset: baseQuery.filter.offset });
    }
    return `${history.location.pathname}?${getQueryRoute(query)}`;
};
